async function fetchFirestoreData() {
  const querySnapshot = await getDocs(collection(db, "Tabel"));
  const data = [];
  querySnapshot.forEach((doc) => {
    data.push({ id: doc.id, ...doc.data() }); // 여기서 각 문서의 id를 포함하도록 수정
  });
  return data;
}

