function Admin({ setShowSearchAndCounting }) {
  const [tableData, setTableData] = useState([]);
  const [isAdminAuthenticated, setIsAdminAuthenticated] = useState(false);
  const [isLoginPopupOpen, setIsLoginPopupOpen] = useState(false);

  const fetchData = async () => {
    const docRef = collection(db, "Tabel");
    const docSnap = await getDocs(docRef);

    const newData = docSnap.docs.map(doc => ({
      ...doc.data(),
      id: doc.id
    })).filter(item => item.upload === 1 || item.upload === 2);

    setTableData(newData);
  }

  const handleApproval = async (item) => {
    const docRef = doc(db, "Tabel", item.id);
    if (item.upload === 2) {
      await updateDoc(docRef, {
        upload: 1
      });
    } else if (item.upload === 1) {
      await updateDoc(docRef, {
        upload: 2
      });
    }
    fetchData();
  };

  const handleDelete = async (item) => {
    const docRef = doc(db, "Tabel", item.id);
    if (window.confirm('삭제하시겠습니까?')) {
      await deleteDoc(docRef);
      alert('삭제 완료!');
      fetchData();
    }
  };

  useEffect(() => {
    const auth = getAuth();
    onAuthStateChanged(auth, (user) => {
      if (user) {
        setIsAdminAuthenticated(true);
      } else {
        setIsAdminAuthenticated(false);
      }
    });
    fetchData();
  }, []);

  useEffect(() => {
    setShowSearchAndCounting(false);
    return () => setShowSearchAndCounting(true);
  }, [setShowSearchAndCounting]);

  const handleLogin = async () => {
    try {
      const auth = getAuth();
      const email = process.env.REACT_APP_ADMIN_EMAIL; // 환경 변수에서 이메일 가져오기
      const password = process.env.REACT_APP_ADMIN_PASSWORD; // 환경 변수에서 비밀번호 가져오기
      await signInWithEmailAndPassword(auth, email, password);
      setIsAdminAuthenticated(true);
      setIsLoginPopupOpen(false);
      alert('로그인 완료!');
    } catch (error) {
      console.error('로그인 에러:', error.message);
      // 에러 코드에 따른 메시지 처리
      if (error.code === 'auth/wrong-password') {
        alert('이메일 또는 비밀번호가 잘못되었습니다.');
      } else {
        alert('로그인에 실패했습니다. 다시 시도해주세요.');
      }
    }
  };


  return (
    <div>
      {isAdminAuthenticated ? (
        <div>
          {tableData.map((data) => (
            <div key={data.id}>
              <div className="information-part">
                <button type="button" className="information-box" id='information-box-admin'>
                  <div className="information-box-top">
                    <div className="information_company_name">{data.company_name}</div>
                    <div className="information_employment_deadline">{data.employment_deadline}</div>
                  </div>
                  <div className="information-box-title">{data.title}</div>
                  <div className="information-box-location">{data.location}</div>
                  <div className="information-box-week">{data.week}</div>
                  <div className="information-box-career">{data.career}</div>
                  <div className="information-box-salary">
                    <div className="information_salary_system">{data.salary_system}</div>
                    <div className="information_salary_amount">{data.salary_amount}</div>
                  </div>
                </button>
              </div>
              <div className='admin-button-under'>
                <button
                  className={data.upload === 1 ? "cancel-button" : "approval-button"}
                  onClick={() => handleApproval(data)}
                >
                  {data.upload === 1 ? "내리기" : "승인"}
                </button>
                <button className='delete-button' onClick={() => handleDelete(data)}>삭제</button>
              </div>
            </div>
          ))}
        </div>
      ) : (
        <div>
          <h4>인증되지 않음</h4>
          <button className='login-button' onClick={() => setIsLoginPopupOpen(true)}>로그인</button>

          {/* 로그인 팝업 */}
          {isLoginPopupOpen && (
            <div className='login-pop'>
              <input className='email-input-box' type="text" placeholder="이메일" />
              <input className='password-input-box' type="password" placeholder="비밀번호" />
              <button className='login-button-pop' onClick={handleLogin}>로그인</button>
              <button className='close-button-pop' onClick={() => setIsLoginPopupOpen(false)}>닫기</button>
            </div>
          )}
        </div>
      )}
    </div>
  );
}

export default App;